$('.user-link').on('click', function(e) {
//  e.preventDefault();//prevent the link from being followed
  var slug = $(this).attr('data-slug');
  var userMeta = userData[slug];
  document.title = (userMeta.name);
  $('.user-name').text(userMeta.name);
  $('.user-splash').css('background-image', 'url(' + userMeta.splash + ')');
  $('.user-image-1').attr('src', userMeta.image1);
  $('.user-image-2').attr('src', userMeta.image2);
  $('.user-image-3').attr('src', userMeta.image3);
  $('.user-image-4').attr('src', userMeta.image4);
  $('.user-image-5').attr('src', userMeta.image5);
  $('.qn1').text(userMeta.qn1);
  $('.qn2').text(userMeta.qn2);
  $('.qn3').text(userMeta.qn3);
  $('.qn4').text(userMeta.qn4);
  $('.qn5').text(userMeta.qn5);
  $('.contact-information1').text(userMeta.contact1);
  $('.contact-information2').text(userMeta.contact2);
  $('.contact-information3').text(userMeta.contact3);

  if (userMeta.colorswitch === true) {
    $('.color-switch').addClass( 'whitetext');
    $('.color-switch').attr('src', 'assets/img/logo-white.svg');
  }else{
    $('.color-switch').removeClass( 'whitetext' );
    $('.color-switch').attr('src', 'assets/img/logo-black.svg');
  };

  if (userMeta.student === true) {
    $('.foreword').addClass( 'hidden');
    $('.student').removeClass( 'hidden');
  }else{
    $('.foreword').removeClass( 'hidden');
    $('.student').addClass( 'hidden');
  };
});

$('.navlist span').click(function(e) {
  $('.navlist span').removeClass('user-active');
  $(this).addClass('user-active');
});

$("img").error(function () {
    $(this).hide();
    // or $(this).css({visibility:"hidden"});
});

